.galery {
  margin-bottom: 130px;
  @include tablet {
    margin-bottom: 200px;
  }

  &__h2 {
    margin-bottom: 50px;
  }

  &__row {
    display: none;
    @include desktop {
      display: grid;
      column-gap: 30px;
      grid-template-columns: repeat(12, 1fr);
      margin-bottom: 30px;
      &:nth-child(3) {
        margin-bottom: 0;
      }
    }
  }

  &__swiper {
    @include desktop {
      display: none;
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    @include transition(transform);
    @include desktop {
      &:hover {
        transform: scale(1.05);
      }
    }
  }
}
