.nav {
  position: absolute;
  top: 23px;

  @include tablet {
    top: 45px;
  }

  @include beforeTablet {
    display: none;
  }

  @include beforeDesktop {
    left: $PaddingLeft767;
  }
  left: $PaddingLeft;

  &--mod-tablet {
    @include beforeTablet {
      display: block;
    }
  }
}

.burger {
  &__icon {
    width: 18px;
    height: 13px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    span {
      width: 100%;
      height: 2px;
      background-color: #000;
      position: relative;
      @include transition(all);
    }

    &.active span:nth-child(1) {
      top: 50%;
      transform: rotate(-45deg) translate(0, -50%);
    }

    &.active span:nth-child(2) {
      top: 50%;
      transform: scale(0) translate(0, -50%);
    }

    &.active span:nth-child(3) {
      bottom: 50%;
      transform: rotate(45deg) translate(0, 50%);
    }
  }

  &__logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 10px;
  }

  &__menu {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: $main-color;
    transform: translateY(-400%);
    @include transition(transform);
    overflow: auto;
    z-index: 100;
  }

  &__menu.active {
    transform: translateY(0);
    overflow: auto;
  }

  &__wrapper {
    position: relative;
  }

  &__nav {
    padding-top: 23px;
    margin-bottom: 20px;
    @include beforeTablet {
      padding-left: 35px;
    }
    @include tablet {
      padding-top: 45px;
    }
  }

  &__list {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    padding-top: 20px;
  }

  &__li {
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    text-decoration: none;
    color: #fff;
    font-size: 70px;
    @extend %Plex700;
    margin-bottom: 10px;
  }
}
