.events {
  margin-bottom: 185px;
  @include tablet {
    margin-bottom: 210px;
  }
  @include desktop {
    margin-bottom: 300px;
  }

  &__h2 {
    margin-bottom: 50px;
  }

  &__item {
    margin-bottom: 60px;
    @include desktop {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 100px;
    }
  }

  &__image {
    @include transition(transform);
    margin-bottom: 20px;
    @include tablet {
      margin-bottom: 40px;
    }
    @include desktop {
      &:hover {
        transform: scale(1.1);
      }
    }
  }

  &__img {
    max-width: 100%;
    height: auto;

    @include beforeDesktop {
      width: 100%;
    }

    &--widthHeight {
      width: 100%;
      max-height: 1000px;
      object-fit: cover;
      object-position: center;
    }
  }

  &__description {
    @include tablet {
      padding-left: 10px;
    }
  }

  &__footnote {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    justify-content: space-between;
    @include beforeDesktop {
      max-width: 470px;
    }

    @include tablet {
      margin-bottom: 30px;
    }

    &--modMain {
      justify-content: flex-start;

      @include beforeTablet {
        justify-content: space-evenly;
      }
    }
  }

  &__type {
    @extend %Plex500;
    font-size: 14px;
    color: #687480;
    margin-right: 30px;
    text-transform: uppercase;

    &--modMain {
      margin-right: calc(50% - 80px);
    }
  }

  &__date {
    @extend %Plex700;
    font-size: 14px;
    line-height: 150%;
    color: $main-color;
  }

  &__title {
    display: inline;
    position: relative;

    &::after {
      @include beforeTablet {
        width: 10px;
        height: 10px;
        right: -20px;
      }

      content: "";
      display: block;
      position: absolute;
      top: 50%;
      right: -40px;
      transform: translateY(-50%);
      background-color: #f14a27;
      width: 15px;
      height: 15px;
    }

    &--mod {
      display: block;
      @include beforeTablet {
        text-align: center;
      }
      @include tablet {
        max-width: 470px;
      }

      @include desktop {
        max-width: 370px;
      }
    }
  }

  &__text {
    @extend %Plex300;
    font-size: 16px;
    line-height: 150%;
    color: #4e4e4e;
    margin-top: 15px;
    @include tablet {
      max-width: 470px;
    }

    @include desktop {
      max-width: 370px;
    }
  }

  &__main {
    position: relative;
  }

  &__main-description {
    min-width: 280px;
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 50%);
    background-color: #fff;
    box-shadow: 0 2px 0 rgba(26, 90, 76, 0.25);
    padding: 20px 5px;

    @include tablet {
      padding: 50px 55px;
    }

    @include desktop {
      padding: 50px 100px;
    }

    & .events__title::after {
      content: none;
    }
  }
}
