.form-section {
  background: url("../images/form_back.jpg") no-repeat;
  background-size: cover;
  background-position: center;
  margin-bottom: 50px;

  &__container {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 85px 20px;
    text-align: center;
    @include desktop {
      padding: 165px 0;
    }
  }

  &__h2 {
    color: #fff;
    text-align: center;
  }

  &__text {
    @extend %Plex400;
    font-size: 18px;
    line-height: 150%;
    color: #fff;
    margin: 20px 0 50px;
    @include tablet {
      margin: 16px 0 65px;
      line-height: 100%;
    }
  }

  &__form {
    height: 50px;
    @include desktop {
      height: 70px;
    }
    display: flex;
  }

  &__email {
    outline: none;
    width: 230px;
    @include desktop {
      width: 470px;
    }
    height: 100%;
    @include tablet {
      margin-right: 30px;
    }

    @extend %Montserrat400;
    font-size: 14px;
    color: #687480;
    padding: 18px 24px;
    @include tablet {
      padding: 24px 28px;
    }
    @include desktop {
      padding-left: 32px;
    }

    &::placeholder {
      font-size: 14px;
      color: #687480;
    }
  }

  &__submit {
    width: 50px;
    @include desktop {
      width: 70px;
    }
    border: 1px solid #f14a27;
    height: calc(100% - 1px);
    background-color: #f14a27;
    position: relative;
    cursor: pointer;

    &__arow {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }
}
