.title {
  @extend %Montserrat700;
  font-size: 45px;
  line-height: 120%;
  color: #000;
  text-transform: uppercase;
  text-align: center;

  @include tablet {
    text-align: start;
    font-size: 54px;
  }

  @media(min-width: 768px) and (max-width: 1024px) {
    font-size: 55px;
  }

  @include desktop {
    font-size: 72px;
  }

  &__h2 {
    @extend %Montserrat700;
    font-size: 48px;
    color: #0f0e08;
    @include beforeTablet {
      text-align: center;
    }
  }

  &__h3 {
    @include beforeTablet {
      font-size: 28px;
    }
    @extend %Montserrat400;
    font-size: 36px;
    color: #0f0e08;
  }

  &__h4 {
    @extend %Plex400;
    font-size: 14px;
    line-height: 140.62%;
    color: #0f0e08;
  }
}
