.swiper {
  width: 100%;
  height: 100%;

  &-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    @include beforeTablet {
      width: 100%;
    }
    width: 45%;
  }

  &-slide img {
    margin-bottom: 30px;
    @include beforeTablet {
      width: 50%;
    }
    @media(max-width: 450px) {
      width: 80%;
    }
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &-pagination {
    bottom: 0 !important;
  }

  &-pagination-bullet {
    width: 9px;
    height: 9px;
    background: $main-color;
  }
}
