.header {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: relative;
  margin-bottom: 70px;

  @include tablet {
    flex-direction: row;
    margin-bottom: 150px;
  }

  &__content {
    flex: 0 0 100%;
    display: flex;

    @media(max-width: 767px) {
      order: 1;
    }
  }

  &__container {
    display: flex;
    align-items: center;
    position: relative;
    max-width: 1320px;
    margin: 0 auto;
    @include tablet {
      padding-left: $PaddingLeft767;
    }

    @include desktop {
      padding-left: $PaddingLeft;
    }
  }

  &__body {
    @include tablet {
      width: 50%;
      padding: 0 36px 0 0;
    }
  }

  &__title {
    @include beforeTablet {
      margin: 20px 0;
    }

    @include desktop {
      margin-bottom: 30px;
    }
  }

  &__hidden {
    display: none;
    @include tablet {
      display: inline;
    }
    opacity: 0;
  }

  &__image {
    position: relative;
    @include tablet {
      padding: 0 0 100vh;
      flex: 0 0 50%;
      transform: translateX(-100%);
    }
  }

  &__logo-link {
    position: absolute;
    z-index: 2;
    left: 50%;
    transform: translate(-50%, 10px);

    @include tablet {
      top: 25px;
      left: 70px;
    }
  }

  &__block-image {
    max-width: 100%;
    width: 100%;

    @include tablet {
      position: absolute;
      height: 100%;
      left: 0;
      top: 0;
      object-fit: cover;
    }
  }

  &__button-wrapper {
    display: flex;
    justify-content: center;

    @include tablet {
      justify-content: start;
    }

    @include desktop {
      justify-content: flex-end;
      padding-right: 20px;
    }
  }

  &__button {
    display: flex;
    width: 280px;
    height: 50px;
    background-color: $main-color;

    @include tablet {
      width: 270px;
      height: 70px;
    }
  }

  &__tickets-link {
    display: flex;
    width: 230px;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border: 1px solid $main-color;
    text-decoration: none;
    @include transition(all);

    & span {
      @extend %Plex500;
      font-size: 16px;
      color: #0f0e08;
    }

    &:hover {
      background-color: $main-color;

      & span {
        color: #fff;
      }
    }

    @include tablet {
      width: 200px;
    }
  }

  &__arow-link {
    position: relative;
    width: 50px;
    background-color: $main-color;

    @include tablet {
      width: 70px;
    }

    &::after {
      content: url("../images/Vector.svg");
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &__event {
    @extend %Plex700;
    display: none;
  }

  @include desktop {
    &__event {
      display: inline;
      position: absolute;
      bottom: 90px;
      left: 70px;
      transform: rotate(-90deg);
      transform-origin: 0 bottom;
      font-size: 14px;
      line-height: 100%;
      color: #687480;
      text-transform: uppercase;
      text-decoration: none;

      &::after {
        content: "";
        display: block;
        position: absolute;
        bottom: 6px;
        left: -19px;
        width: 1px;
        height: 70px;
        background: $main-color;
        transform: rotate(-90deg);
        transform-origin: 0 100%;
      }
    }
  }
}
