.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;

  @include tablet {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 30px;
  }

  @include desktop {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
  }

  &__item {
    @for $i from 1 through 12 {
      @for $k from $i through 12 {
        &--#{$i}-#{$k} {
          grid-column: #{$i} / #{$k + 1};
        }
      }
    }
  }
}
