.footer {
  &__top {
    margin-bottom: 80px;
    @include beforeTablet {
      grid-template-rows: repeat(5, auto);
      row-gap: 40px;
    }

    @include beforeDesktop {
      grid-template-rows: repeat(3, auto);
      row-gap: 64px;
    }
  }

  &__socialWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include tablet {
      height: 160px;
      flex-direction: column;
      align-items: stretch;
    }
  }

  &__soc-icons {
    display: flex;
    justify-content: space-between;
    @include beforeTablet {
      width: 60px;
    }
  }

  &__instagram {
    @include beforeDesktop {
      margin-right: 30px;
    }

    @include beforeTablet {
      margin-right: 0;
    }
  }

  &__instagram,
  &__facebook {
    & path {
      @include transition(fill);
    }
  }

  &__instagram:hover,
  &__facebook:hover {
    & path {
      fill: $main-color;
    }
  }

  &__h4 {
    margin-bottom: 20px;
  }

  &__text {
    @extend %Plex400;
    font-size: 14px;
    line-height: 150%;
    color: #0f0e08;
    &--dayOff {
      margin-top: 30px;
    }

    &--mod-bottom:nth-child(1) {
      margin-bottom: 19px;
    }
  }

  &__schedule {
    @extend %Plex600;
    font-size: 14px;
    line-height: 150%;
    color: #0f0e08;
  }

  &__item.grid__item {
    &--1-1 {
      @include beforeTablet {
        grid-column: 1 / 3;
      }
    }

    &--3-4 {
      @include beforeDesktop {
        grid-column: 2 / 4;
      }

      @include beforeTablet {
        grid-column: 1 / 2;
      }
    }

    &--5-6 {
      @include beforeDesktop {
        grid-column: 4 / 6;
      }

      @include beforeTablet {
        grid-column: 2 / 3;
      }
    }

    &--8-9 {
      @include beforeDesktop {
        grid-column: 2 / 4;
        grid-row: 3 / 4;
      }

      @include beforeTablet {
        grid-column: 1 / 2;
        grid-row: 4 / 5;
      }
    }

    &--10-10 {
      @include beforeDesktop {
        grid-column: 4 / 5;
        grid-row: 3 / 4;
      }

      @include beforeTablet {
        grid-column: 2 / 3;
        grid-row: 4 / 5;
      }
    }
  }

  &__adress {
    text-decoration: none;
    color: #0f0e08;
    @include transition(color);
    &:hover {
      color: $main-color;
    }
  }

  &__item.grid__item--7-7 {
    position: relative;
    @include beforeDesktop {
      grid-row: 2 / 3;
      grid-column: 2 / 6;
    }

    @include beforeTablet {
      grid-column: 1 / 3;
      grid-row: 3 / 4;
    }
  }

  &__link {
    @extend %Plex400;
    font-size: 14px;
    line-height: 100%;
    color: #0f0e08;
    text-decoration: none;
  }

  &__line {
    position: absolute;
    height: 1px;
    width: 100%;
    left: 0;
    top: 0;
    background: rgba(26, 90, 76, 0.5);

    @include desktop {
      left: 50%;
      transform: rotate(90deg);
      transform-origin: 0 bottom;
    }
  }

  &__menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }

  &__bottom {
    padding-bottom: 35px;
    @extend %Plex300;
    font-size: 12px;
    line-height: 140.62%;
    text-align: center;
    color: #0f0e08;
    @include tablet {
      display: flex;
      justify-content: space-between;
    }
  }

  &__info {
    &:first-child {
      margin-bottom: 10px;
    }
  }

  &__buttonUp {
    display: none;
    @include beforeTablet {
      position: fixed;
      bottom: 20px;
      right: 20px;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      border: 1px solid $main-color;
      align-items: center;
      justify-content: center;
      z-index: 101;
    }
  }
}
